"use strict";

let theme = localStorage.getItem("theme");
if (theme === "dark") document.body.classList.add("dark");
else document.body.classList.add("light");

const themeBtn = document.querySelector(".themeBtn");
const inputDate = document.querySelectorAll(".inputDate");
const select = document.querySelector(".select");
const radioBtns = document.querySelectorAll(".radioBtn");
const radioBtnFemale = document.querySelector("#female");
const radioBtnMale = document.querySelector("#male");
const inputsNumber = document.querySelectorAll(".inputNumber");
const inputRange = document.querySelector(".inputRange");

let gender;

radioBtns.forEach((btn) => {
  btn.addEventListener("click", () => {
    console.log("radioBtn clicked");

    if (btn.id === "female") {
      radioBtnFemale.classList.add("radioBtn--active");
      radioBtnMale.classList.remove("radioBtn--active");
      gender = "female";
    } else {
      radioBtnMale.classList.add("radioBtn--active");
      radioBtnFemale.classList.remove("radioBtn--active");
      gender = "male";
    }
  });
});

// show select list
select.addEventListener("click", () => {
  const selectList = document.querySelector(".select__list");
  const selectListItem = document.querySelector(".select__listItem");
  selectList.classList.toggle("select__list--active");

  selectListItem.forEach((item) => {
    item.addEventListener("click", () => {
      select.textContent = item.textContent;
    });
  });
  selectListItem.forEach((item) => {
    item.addEventListener("keydown", (e) => {
      if (e.key === "Enter") {
        select.textContent = item.textContent;
      }
    });
  });
  window.addEventListener("click", (e) => {
    closeList(e);
  });
  window.addEventListener("touch", (e) => {
    closeList(e);
  });
  select.addEventListener("keydown", (e) => {
    if (e.key === "Enter") closeList();
  });

  // hide select list
  function closeList(e) {
    console.log(e);
    if (!e.target.classList.contains("select__list")) {
      selectList.classList.remove("select__list--active");
    }
  }
});

inputRange.addEventListener("click", () => {
  changeHeightValue();
});
inputRange.addEventListener("mousemove", () => {
  changeHeightValue();
});
inputRange.addEventListener("keydown", (e) => {
  if (e.key === "ArrowLeft" || e.key === "ArrowRight") {
    changeHeightValue();
  }
});

inputsNumber.forEach((input) => {
  input.addEventListener("input", (e) => {
    if (e.target.value < 1) {
      e.target.value = 1;
    } else {
      if (e.target.name === "age") {
        if (e.target.value > 199) e.target.value = 199;
      } else if (e.target.value > 999) e.target.value = 999;
    }
  });
});

// Funkcja pokazująca wartość inputa typu range

function changeHeightValue() {
  const inputRangeValue = document.querySelector(".inputRange__value");
  inputRangeValue.textContent = parseInt(inputRange.value);
}

//change theme on a page
themeBtn.addEventListener("click", () => {
  console.log("click");
  if (theme === "dark") {
    document.body.classList.remove("dark");
    document.body.classList.add("light");
    theme = "light";
  } else {
    document.body.classList.remove("light");
    document.body.classList.add("dark");
    theme = "dark";
  }
  localStorage.setItem("theme", theme);
});

//focus on date type input
inputDate.forEach((input) =>
  input.addEventListener("change", () => {
    input.classList.add("inputDate--focus");
  })
);
